<template>
  <div class="preview">

    <!-- default -->
    <myImage  :mysrc="defaults[0].pathLong" myindex="2" />
    <myImage  :mysrc="defaults[1].pathLong" myindex="0" />
    <myImage  :mysrc="defaults[2].pathLong" myindex="3" />
    <myImage  :mysrc="defaults[3].pathLong" myindex="1" />


    <!-- color -->
    <div class="group">
      <myImage :mysrc="color[0].pathLong" myindex="10" />
      <myImage :mysrc="color[1].pathLong" myindex="11" />
      <myImage :mysrc="color[2].pathLong" myindex="2" />
      <myImage :mysrc="color[3].pathLong" myindex="13" />
      <myImage :mysrc="color[4].pathLong" myindex="2" />
    </div>

    <div class="group" v-for="s in selection" v-if="s.value!=='false'" :key="s.name">
        <!-- {{s}} -->
        <transition name="fade">
          <myImage :mysrc="getImage(s)" :myindex="s.idx" :key="s.name+s.value"/>
        </transition>

    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import myImage from '@/components/myImage.vue'

export default {
  name: 'preview',
  components: {
    myImage
  },
  data() {
    return {

    };
  },
  computed: {
    // selection:{
    //   get () {
    //     return this.$store.state.selection.filter(x=>x.value!="false" && x.cat!="body")
    //   }
    //
    // },
    selection(){
      return this.$store.state.selection.filter(x=>x.value!=="false")
    },
    images() {
      return this.$store.state.images
    },
    defaults(){
      return this.images.filter(x=>x.cat=="default")
    },
    color(){
      let color = this.selection.find(x=>x.name=="body").value
      return this.images.filter(x=>x.subcat==color)
    },

  },
  methods: {
    getImage(s){
      if(s.name!="body"){
        let arr = this.images.filter(x=>x.cat==s.name)
        let idx = parseInt(s.value)-1
        if(arr[idx]){
          return arr[idx].pathLong

        }

      }

      // console.log(s.value)
      // console.log(arr[0].pathLong)
    },
    importAll(r) {

      let cats = [];
      r.keys().forEach(key => {
        let cat1 = key.split('/')[1];
        let cat2;
        cats.push(cat1)

        if(cat1=="body"){
          cat2 = key.split('/')[2];
        }
        this.$store.state.images.push({ pathLong: r(key), pathShort: key, cat: cat1, subcat:cat2})
      });
      cats = [...new Set(cats)]


      for(let cat of cats){
        let idx = 20;
        if(cat=="body"){
          idx=5
        }
        if(cat=="tires"){
          idx=2
        }
        if(cat=="wheels"){
          idx=2
        }
        if(cat=="grill"){
          idx=19
        }
        this.$store.state.cats.push(cat)
        let val = "false"
        if(cat=="body"){val="black"}
        if(cat=="tires"){val=1}
        if(cat=="wheels"){val=1}
        // if(cat=="grill"){val=19}

        this.$store.state.selection.push({name:cat,value:val,idx:idx.toString()})
      }


    },
  },
  created(){
    this.importAll(require.context('@/assets/images/', true, /\.png$/));
  },
  watch: {

  }
}
</script>

<style lang="css">
.preview{
  background: #454545;
  position:relative;
  flex: 1;
  min-width: 400px;
}
.preview:after{
  content: '';
  display: block;
  padding-bottom: 61.82%;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.24s ease-in-out;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
