<template>
  <div class="cat">
    <h3>{{cat}}</h3>

    <!-- <div class="title">
      <md-radio v-model="radio" value="false" v-if="cat!='body'">None</md-radio>
    </div> -->
    <!-- <md-radio v-model="radio" :value="false">Boolean</md-radio>
    <md-radio v-model="radio" value="my-radio">String</md-radio>
    <md-radio v-model="radio" :value="objA">Object A</md-radio>
    <md-radio v-model="radio" :value="objB">Object B</md-radio>
    <md-radio v-model="radio">No Value</md-radio>
    <md-radio v-model="radio" disabled>Disabled</md-radio> -->

    <md-radio v-model="radio" value="false" v-if="cat!='body' && cat!='tires' && cat!='wheels'">None</md-radio>
    <md-radio v-model="radio" v-for="opt in opts" :value="opt">{{opt}}</md-radio>


  </div>
</template>

<script>
  export default {
    name: 'RegularRadio',
    data: () => ({
      // radio: "false",
    }),
    props:{
      cat: String
    },
    computed: {
      selection(){
        return this.$store.state.selection
      },
      // radio(){
      //    return this.$store.state.selection.find(o=>o.name==this.cat).value
      // },
      radio:{
        get () {
          return this.selection.find(o=>o.name==this.cat).value
        },
        set (value) {
          this.$store.state.selection.find(o=>o.name==this.cat).value = value.toString()
          this.$store.state.selection.push("reset")
          this.$store.state.selection.pop()
        }
      },
      opts(){
        let opts=[]
        if(this.cat=="body"){
          let arr = this.images.filter(x=>x.cat=="body").map(x=>x.subcat)
          opts = [...new Set(arr)]
        }else{
          let len = this.images.filter(x=>x.cat==this.cat).length

          for(let i=1; i<=len; i++){
            opts.push(i.toString())
          }
        }

        return opts

      },
      images() {
        return this.$store.state.images
      },
    },
    methods: {
    },
    mounted(){
      this.radio = this.selection.find(o=>o.name==this.cat).value

    },
    watch: {
      radio: function(val) {
        // console.log("radio changed")
      },

    }
  }
</script>

